import { AppBar, Button, FormControl, Grid, TextField, Typography } from '@mui/material'
import { Box } from '@mui/system'
import React, { useState, useRef, useEffect } from 'react'
import { useNavigate } from 'react-router-dom';

function Kirjautuminen(props) {

    const navigate = useNavigate();

    const tunnus = useRef();
    const salasana = useRef();
    const [virheilmoitus, setVirheilmoitus] = useState({});

    const tarkistaTiedot = () => {
      // e.preventDefault();
      console.log("jee");

      let virheet = {};

      if(!tunnus.current.value)
      {
      virheet = {...virheet, tunnus : "Syt kyttjtunnus"}
      }
      if(!salasana.current.value)
      {
      virheet = {...virheet, salasana : "Syt salasana"}
      }
      if (Object.entries(virheet).length > 0) {

          setVirheilmoitus({...virheet});
      }
      else
      {
        resetoiVirheet();
        let tiedot = props.kayttajaLista.find((data) => {
          if(data.tunnus === tunnus.current.value){
            return(data)
          }
        });

        //Lytyk kyttjtunnusta listasta
        if(tiedot)
        {
          if(tiedot.salasana === salasana.current.value)
          {
            resetoiVirheet();
            props.kirjauduUlosSisaan(tiedot);
          }
          else{
            virheet = {...virheet , kirjautuminen : "Virheellinen kyttjtunnus tai salasana"}
          }
        }
        else{
          virheet = {...virheet , kirjautuminen : "Virheellinen kyttjtunnus tai salasana"}
        }
        setVirheilmoitus({...virheet});
      }
    }

    const resetoiVirheet = () => {
      setVirheilmoitus({});
    }

    useEffect(() => {

      resetoiVirheet();

    }, [props.kirjautunut]);

  return (
    <>
      <Box textAlign="right" sx={{ pt:1 }}>
      <AppBar position="static" sx={{borderRadius:"5px" , p:1, backgroundColor:"#0099ff"}}>
      <Grid container
      direction="row"
      justifyContent="flex-end"
      alignItems="top">
      {props.kirjautunut ? 
      [<Grid item key="1">
        <Typography  variant="h6" >
        Kirjautunut kyttjll: {props.kayttaja ? props.kayttaja.tunnus : null}
        </Typography> 
      </Grid>,
      <Grid item key="2"><Button 
      variant="contained"
      color="secondary"
      // size="small"
      onClick={() => {props.kirjauduUlosSisaan()}}
      sx={{ml: 1}}
      >Kirjaudu ulos</Button></Grid>] 
      //Ei ole kirjautunut
      : 
      [<Grid item key="1"><TextField 
      name="tunnus"
      variant="outlined"
      label="Kyttjtunnus"
      size="small"
      error={Boolean(virheilmoitus.tunnus)}
      helperText={virheilmoitus.tunnus}
      inputRef={tunnus}
      /></Grid>
      ,
      <Grid item key="2"><TextField
      key= "2"
      name="salasana"
      variant="outlined"
      type="password"
      label="Salasana"
      size="small"
      sx={{ml:"5px"}}
      error={Boolean(virheilmoitus.salasana)}
      helperText={virheilmoitus.salasana}
      inputRef={salasana}
      /></Grid>
      ,
      <Grid item key="3"><Button
      variant="contained"
      color="primary"
      onClick={() => {tarkistaTiedot()}}
      sx={{ml: 1}}
      >Kirjaudu</Button>
      </Grid>
      ,
      <Grid item key="4">
      <Button
      variant="contained"
      color="secondary"
      sx={{ml: 1}}
      onClick={() => navigate("/rekisteri")}
      >Rekisteridy</Button></Grid>]
      }
      </Grid>
      <Typography 
        color="error" 
        variant="overline" 
        fontWeight="bold" 
        textAlign="center">{virheilmoitus.kirjautuminen}
      </Typography>
      </AppBar>
      </Box>
    </>
  )
}

export default Kirjautuminen