import React, { useEffect, useRef, useState } from 'react'
import { Box, Typography, TextField, Button, Container} from '@mui/material';
import { useNavigate } from 'react-router-dom';

function Rekisterointi(props) {
    const navigate = useNavigate();

    const tunnus = useRef();
    const salasana = useRef();
    const sposti = useRef();
    const [virheilmoitus, setVirheilmoitus] = useState({});

    const kasitteleSyotto = (e) => {

        e.preventDefault();

        let virheet = {};

        if(!tunnus.current.value)
        {
        virheet = {...virheet, tunnus : "Kyttjtunnus puuttuu"}
        }
        if(!salasana.current.value)
        {
        virheet = {...virheet, salasana : "Salasana puuttuu"}
        }
        if(!sposti.current.value)
        {
        virheet = {...virheet, sposti : "Shkposti puuttuu"}
        }
        if (Object.entries(virheet).length > 0) {
            setVirheilmoitus({...virheet});
        }
        else{
        setVirheilmoitus({});
        //Tarkistaa lytyyk tunnuksia tai shkpostia listasta
        if(props.kayttajaLista.length > 0){
            props.kayttajaLista.forEach((data) => {
                if(data.tunnus === tunnus.current.value){
                    virheet = {...virheet, tunnus : "Kyttjtunnus on jo kytss. Valitse toinen."};
                } 
                if(data.sposti === sposti.current.value){
                    virheet = {...virheet, sposti : "Shkposti on jo kytss. Valitse toinen."}
                }
            });
        }
        if (Object.entries(virheet).length > 0) {
            setVirheilmoitus({...virheet});
        }
        else{
            setVirheilmoitus({});
            let uusiKayttaja = {
                tunnus : tunnus.current.value,
                salasana : salasana.current.value,
                sposti : sposti.current.value
            }
            // props.setKayttaja(uusiKayttaja);
            props.lisaaKayttaja(uusiKayttaja);
            }
        }
    }

    const tarkistaKirjautuminen = () => {
        if(props.kirjautunut === true)
        {
            navigate("/");
        }
    }

    useEffect(() => {

        tarkistaKirjautuminen();

    }, [props.kirjautunut])


  return (
      <Container maxWidth ="xs" sx={{p : 5}}>
    <form onSubmit={kasitteleSyotto}>

        <Typography variant="h4">Rekisterinti</Typography>
        <Box sx={{mt:"10px"}}>
            <Typography variant="h5">Syt kyttjtunnus: </Typography>
            <TextField 
            name="tunnus"
            variant="outlined"
            label="Kyttjtunnus"
            margin="dense"
            error={Boolean(virheilmoitus.tunnus)}
            helperText={virheilmoitus.tunnus}
            inputRef={tunnus}
            />
        </Box>

        <Box sx={{mt:"10px"}}>
            <Typography variant="h5">Syt salasana: </Typography>
            <TextField 
            name="salasana"
            variant="outlined"
            type="password"
            label="Salasana"
            margin="dense"
            error={Boolean(virheilmoitus.salasana)}
            helperText={virheilmoitus.salasana}
            inputRef={salasana}
            />
        </Box>

        <Box sx={{mt:"10px"}}>
            <Typography variant="h5">Syt shkpostiosoite: </Typography>
            <TextField 
            name="sposti"
            variant="outlined"
            label="Shkposti"
            margin="dense"
            error={Boolean(virheilmoitus.sposti)}
            helperText={virheilmoitus.sposti}
            inputRef={sposti}
            />
        </Box>
        
        <Box sx={{mt:"10px"}}>
            <Button
                type="submit"
                variant="contained"
                color="primary"
                size='larger'
                >Rekisteridy</Button>
        </Box>

        <Box sx={{mt:"20px"}}>
        <Button
                type="button"
                variant="contained"
                color="secondary"
                size='larger'
                // startIcon={<HomeIcon />}
                onClick={() => navigate("/")}
                >Peruuta</Button>
        </Box>
        <Typography sx={{color:"red"}}>{virheilmoitus.kirjautuminen}</Typography>
    </form>
    </Container>
    );
}

export default Rekisterointi