import { LocalizationProvider } from '@mui/lab'
import AdapterDateFns from '@mui/lab/AdapterDateFns';
import { Box, Button, TextField, Typography } from '@mui/material'
import React, { useEffect, useRef, useState } from 'react'
import { fi } from 'date-fns/locale'
import { useNavigate } from 'react-router-dom';
import { v4 as uuid } from 'uuid';

function LisaaIlmoitus(props) {

    const navigate = useNavigate();

    const otsikko = useRef();
    const teksti = useRef();

    const [virheilmoitus, setVirheilmoitus] = useState({});

    const kasitteleSyotto = (e) => {
        e.preventDefault();

        let virheet = {};

        if(!otsikko.current.value)
        {
        virheet = {...virheet, otsikko : "Lis otsikko"}
        }
        if(!teksti.current.value)
        {
        virheet = {...virheet, teksti : "Lis ilmoitusteksti"}
        }
        if (Object.entries(virheet).length > 0) {

            setVirheilmoitus({...virheet});
      
        }
        else
        {
            setVirheilmoitus({});
            let uusiKontentti = {
                                id : uuid(),
                                otsikko : otsikko.current.value,
                                teksti : teksti.current.value,
                                kaupunki : props.valinnat.kaupunki,
                                kategoria : props.valinnat.kategoria,
                                kayttaja : props.kayttaja.tunnus,
                                aika : new Date().getTime()
                                }
            props.lisaaIlmoitus(uusiKontentti);
            navigate("/kontentti");
        }
    }

    const tarkistaKirjautuminen = () => {
        if(!props.kirjautunut){
            navigate("/kontentti");
        }
    }
    useEffect(() => {

        tarkistaKirjautuminen();
  
      }, [props.kirjautunut]);
    
  return (
    <LocalizationProvider dateAdapter={AdapterDateFns} locale = {fi}>
        <Typography variant="h4">{props.valinnat.kaupunki}/{props.valinnat.kategoria}</Typography>

    <form onSubmit={kasitteleSyotto}>

        <Box sx={{mt:"20px"}}>
        <Typography variant="h6">Kirjoita aiheesta otsikko (maks. 25 merkki)</Typography>
        <TextField 
        name="otsikko"
        variant="outlined"
        label="Otsikko"
        margin="dense"
        sx={{width:"250px"}}
        inputProps={{ maxLength: 25 }}
        error={Boolean(virheilmoitus.otsikko)}
        helperText={virheilmoitus.otsikko}
        inputRef={otsikko}
        />
        </Box>

        <Box sx={{mt:"20px"}}>
        <Typography variant="h6">Kirjoita ilmoituksesta kuvausteksti (maks. 500 merkki)</Typography>
        <TextField 
        name="teksti"
        variant="outlined"
        label="Ilmoitusteksti"
        margin="dense"
        multiline
        rows={6}
        fullWidth
        inputProps={{ maxLength: 500 }}
        error={Boolean(virheilmoitus.teksti)}
        helperText={virheilmoitus.teksti}
        inputRef={teksti}
        />
        </Box>

        <Box sx={{mt:"10px"}}>
        <Button
                type="submit"
                variant="contained"
                color="success"
                sx={{mt: 1}}
                >Lis ilmoitus</Button>
        </Box>
        <Box>
        <Button
                variant="contained"
                color="secondary"
                sx={{mt: 1}}
                onClick={() => navigate("/kontentti")}
                >Peruuta</Button>

        </Box>

    </form>
    </LocalizationProvider>
  )
}

export default LisaaIlmoitus