import { Button, Container, List, ListItem, ListItemButton, ListItemText,Typography } from '@mui/material';
import { Box } from '@mui/system';
import React, { useEffect, useState } from 'react'
import { format } from 'date-fns';
import { useNavigate } from 'react-router-dom';

function Kontentti(props) {

    const navigate = useNavigate();
    const [loydot, setLoydot] = useState([]);

    const jarjestaTiedot = (e) => {
        let jarjestetyt = [...e];
        jarjestetyt.sort((a, b) =>{
            let dateA = new Date(a.aika);
            let dateB = new Date(b.aika);
            return dateB - dateA;
            });
        return(jarjestetyt);
    }

    const tarkistaKontentti = () => {
        let kopio = [];
        if(props.kontentti){
            props.kontentti.forEach(data => {
                if(data.kaupunki === props.valinnat.kaupunki && data.kategoria === props.valinnat.kategoria)
                 {
                    kopio = [...kopio, data];
                    kopio = jarjestaTiedot(kopio);
                    setLoydot([...kopio]);
                 }
            });
        }
    }

    useEffect(() => {

        tarkistaKontentti();
  
      }, []);

  return (
    <Container maxWidth ="sm" align="center" sx={{mt:"10px"}}>
    <Typography variant="h4">{props.valinnat.kaupunki}/{props.valinnat.kategoria}</Typography>
    <List>
        {loydot.map((data, idx) => {
            return(
                <ListItem key={idx} divider={true}>
                    <ListItemButton 
                    onClick={() => navigate(`/ilmoitus/${data.id}`)}>
                    <ListItemText 
                    primary={data.otsikko}
                    secondary={
                            `Kyttj : ${data.kayttaja}, ${format(data.aika, "d.M.y HH:mm")}`
                    }/>
                {/* <Typography variant="h4">{data.otsikko} {data.kaupunki} {data.kategoria} {data.teksti}</Typography> */}
                    </ListItemButton>
                </ListItem>
        );})}
    </List>
    {loydot.length > 0 ? null : 
    <Typography variant="h4" sx={{mt:20}}>Tyhjlt nytt...</Typography>}
    
    {props.kirjautunut ? <Box><Button
    variant="contained"
    color="success"
    sx={{mt: 1}}
    onClick={() => navigate("/lisaailmoitus")}
    >Lis ilmoitus</Button>
    </Box> : <Typography variant="h6">Kirjaudu sisn listksesi ilmoituksen</Typography>}
    <Box>
    <Button
    variant="contained"
    color="secondary"
    sx={{mt: 1}}
    onClick={() => navigate("/")}
    >Palaa</Button>
    </Box>

    </Container>
  )
}

export default Kontentti