import { Button, List, ListItem, ListItemButton, ListItemText, Typography } from '@mui/material';
import React, { useState } from 'react'

function Kategoriat(props) {

    const tarkistaIndeksi = () => {
        if(props.valinnat)
        {
            return(props.valinnat.indeksi);
        }
        else
        {
            return(0);
        }
    }
    const tarkistaKaupunki = () => {
        if(props.valinnat)
        {
            return(props.valinnat.kaupunki);
        }
        else
        {
            return("");
        }
    }
    const [indeksi , setIndeksi] = useState(tarkistaIndeksi());
    const [kaupunki, setKaupunki] = useState(tarkistaKaupunki());

    //Tarkistaa kuinka monta lytyy kontentti arraysta
    const tarkistaMaara = (e) => {
        let maara = 0;
        if(indeksi === 0){
            props.kontentti.forEach(data => {
                if(data.kaupunki === e){
                    maara++;
                }
            })
        }
        if(indeksi === 1){
            props.kontentti.forEach(data => {
                if(data.kaupunki === kaupunki && data.kategoria === e){
                    maara++;
                }
            })
        }
        return(maara);
    }

    const tiedot = 
    [["Helsinki",
    "Espoo",
    "Tampere",
    "Vantaa",
    "Oulu",
    "Turku",
    "Jyvskyl",
    "Kuopio",
    "Lahti",
    "Pori"],
    
    ["Urheilu",
    "Messut",
    "Lapsille ja nuorille",
    "Kirpputorit",
    "Musiikki",
    "Palvelut ja ty",
    "Osto ja myynti",
    "Harrastus"]];


        const tarkistaTila = (e) => {
            //Kaupungit
            if(indeksi === 0){
                setIndeksi(indeksi + 1);
                setKaupunki(e);
            }
            //Kategoriat
            if(indeksi === 1){
                if(e){
                    props.siirryKontenttiin({kaupunki : kaupunki, kategoria : e , indeksi : indeksi});
                }
                else{
                    setIndeksi(indeksi - 1);
                    setKaupunki("");
                }
            }
        }

    return(
        <>
        <Typography variant="h4">{indeksi > 0 ? kaupunki : null}</Typography>
        <List>
                {tiedot[indeksi].map((data, idx) => {
                    return(
                        <ListItem key={idx} divider={true} sx={{mx: "auto", width: 200 }}>
                        <ListItemButton 
                            onClick={() => tarkistaTila(data)}>
                        {/* <ListItemText sx={{fontWeight:"bold"}} primary={`${data} (${tarkistaMaara(data)})`}/> */}
                        <Typography variant="h6">{data} ({tarkistaMaara(data)})</Typography>
                        </ListItemButton>
                        </ListItem>
                    );
                } ) }
                {indeksi !== 0 ? <Button
                variant="contained"
                color="secondary"
                sx={{mt: 1}}
                onClick={() => tarkistaTila()}
                >Palaa</Button> : null}
        </List>
        </>
    );
}

export default Kategoriat