import { Button, Container, IconButton, Typography } from '@mui/material';
import { Box } from '@mui/system';
import React, { useEffect, useState } from 'react'
import { useNavigate, useParams } from 'react-router-dom';
import DeleteIcon from '@mui/icons-material/Delete';
import { format } from 'date-fns';

function Ilmoitus(props) {

    const { indeksi } = useParams();
    const [tama, setTama] = useState({});
    const navigate = useNavigate();
    const [tarkistettu, setTarkistettu] = useState(false);

    const suoritaPoisto = () => {
      let dataKopio = props.kontentti.filter((data) => {
          return (data.id !== tama.id);
      });
      props.setKontentti([...dataKopio]);
      navigate("/kontentti");
  }

    useEffect( () => {
        let loyto = props.kontentti.find((tieto) => {
            return (tieto.id === indeksi);
        });
        setTama(loyto);
        setTarkistettu(true);
      }, [])

  return (
    <>
      <Typography variant="h4">{props.valinnat.kaupunki}/{props.valinnat.kategoria}</Typography>
      <Box sx={{border:2 , borderRadius:"10px" , p:5}}>
      <Box sx={{mt:"5px"}}>
      {/* <Typography variant="h6">{tama ? `${tama.kayttaja}, ${format(tama.aika, "d.M.y HH:mm")}`: null}</Typography> */}
      <Typography variant="h6" textAlign="left">{tarkistettu ? `${tama.kayttaja}, ${format(tama.aika, "d.M.y HH:mm")}` : null}</Typography>
      {props.kayttaja ? props.kayttaja.tunnus === tama.kayttaja ? 
      <Button
      type="button"
      variant="contained"
      color="error"
      // size='larger'
      startIcon={<DeleteIcon />}
      onClick={suoritaPoisto}
      >Poista
      </Button> : null : null}
      </Box>
      <Box sx={{mt:"20px"}}>
      <Typography variant="h3">{tama.otsikko}</Typography>
      </Box>

      <Box sx={{mt:"20px"}}>
      <Typography variant="h6">{tama.teksti}</Typography>
      </Box>

      <Box sx={{mt:"20px"}}>
      <Button
                  variant="contained"
                  color="secondary"
                  onClick={() => navigate("/kontentti")}
                  >Palaa
          </Button>
      </Box>
    </Box>
    </>
  )
}

export default Ilmoitus