import {Route, Routes} from "react-router-dom";
import Aloitus from "./components/Aloitus";
import { useEffect, useState } from "react";
import Otsikko from "./components/Otsikko";
import Kirjautuminen from "./components/Kirjautuminen";
import Rekisterointi from "./components/Rekisterointi";
import Kontentti from "./components/Kontentti";
import LisaaIlmoitus from "./components/LisaaIlmoitus";
import Ilmoitus from "./components/Ilmoitus";
import { Container } from "@mui/material";


function App() {
    const [kontentti, setKontentti] = useState([]);
    const [valinnat, setValinnat] = useState();
    const [kirjautunut, setKirjautunut] = useState(false);
    const [kayttajaLista, setKayttajaLista] = useState([]);
    const [kayttaja, setKayttaja] = useState();

    const lisaaKayttaja = (e) => {
      setKayttaja(e);
      //Listn kyttj
      if(kayttajaLista.length > 0){
        setKayttajaLista([...kayttajaLista , e]);
      }
      else{
        setKayttajaLista([e]);
      }
      setKirjautunut(true);
    }

    const lisaaIlmoitus = (e) => {
      let kopio = [...kontentti];
      setKontentti([...kopio, e]);
    }

    const poistaKontentti = (e) => {
      setKontentti(e);
    }

    const tallennaKayttajat = () => {
      localStorage.setItem("kayttajalista", JSON.stringify(kayttajaLista));
    }

    const tallennaKontentti = () => {
      localStorage.setItem("kontenttilista", JSON.stringify(kontentti));
    }

    const kirjauduUlosSisaan = (e) => {
      if(e){
        setKayttaja(e);
        setKirjautunut(true);
      }
      else{
        setKayttaja();
        setKirjautunut(false);
      }
    }

    const paivitaLista = () => {
      if (localStorage.getItem("kayttajalista")) {
        setKayttajaLista(JSON.parse(localStorage.getItem("kayttajalista")));
      }
      if (localStorage.getItem("kontenttilista")) {
        setKontentti(JSON.parse(localStorage.getItem("kontenttilista")));
      }
    }

    const resetoiKaikki = () => {
      setKontentti([]);
      setKirjautunut(false);
      setKayttaja();
      setKayttajaLista([]);
    }

    useEffect(() => {

      paivitaLista();

    }, []);

    useEffect(() => {

      tallennaKayttajat();

    }, [kayttajaLista]);

    useEffect(() => {

      tallennaKontentti();

    }, [kontentti]);


  return (
      <Container maxWidth ="md" align="center" sx={{backgroundColor:"lightblue" , borderRadius:"20px", minHeight:"700px" }}>
          <Kirjautuminen 
          kirjautunut = {kirjautunut} 
          kayttajaLista = {kayttajaLista} 
          kayttaja = {kayttaja} 
          setKirjautunut = {setKirjautunut}
          setKayttaja = {setKayttaja}
          kirjauduUlosSisaan = {kirjauduUlosSisaan}
          />
          <Otsikko resetoiKaikki = {resetoiKaikki}>Ilmoitustaulu</Otsikko>
          <Routes>
            <Route path ="/" exact element={<Aloitus setValinnat={setValinnat} valinnat = {valinnat} kontentti = {kontentti} />}/>
            <Route path ="/rekisteri" exact element={<Rekisterointi 
            kayttajaLista = {kayttajaLista} 
            kirjautunut = {kirjautunut} 
            setKayttaja = {setKayttaja}
            lisaaKayttaja = {lisaaKayttaja}
            setKayttajaLista = {setKayttajaLista}
            />}/>

            <Route path ="/kontentti" exact element={<Kontentti 
            kontentti = {kontentti} 
            valinnat = {valinnat} 
            kirjautunut = {kirjautunut}/>}/>

            <Route path ="/lisaailmoitus" exact element={<LisaaIlmoitus 
            kontentti = {kontentti} 
            valinnat = {valinnat} 
            kirjautunut = {kirjautunut}            
            kayttaja = {kayttaja}
            lisaaIlmoitus = {lisaaIlmoitus}/>}/>

          <Route path ="/ilmoitus/:indeksi" element={<Ilmoitus
          valinnat = {valinnat} 
          kontentti = {kontentti} 
          kayttaja = {kayttaja}
          setKontentti = {setKontentti}/>}/>
          </Routes>
      </Container>
  );
}

export default App;