import { Container} from '@mui/material'
import { useNavigate } from 'react-router-dom';
import Kategoriat from './Kategoriat';

function Aloitus(props) {

    const navigate = useNavigate();

    const siirryKontenttiin = (e) => {
        props.setValinnat(e);
        navigate("/kontentti");
    }

    return (
        <Container maxWidth ="sm" align="center" sx={{mt:"10px"}}>
        <Kategoriat siirryKontenttiin={siirryKontenttiin} valinnat = {props.valinnat} kontentti = {props.kontentti}/>
        </Container>
  );
}

export default Aloitus