import { Button, Typography } from '@mui/material'
import { Box } from '@mui/system'

function Otsikko(props) {
  
  return (
    <>
    <Typography variant="h3" sx={{mt: 2, borderBottom:"solid 3px black"}}
    >{props.children}</Typography>
    <Box textAlign="right">
    <Button 
    variant="contained"
    color="error"
    size="small"
    align
    sx={{mt:2}}
    onClick={() => {props.resetoiKaikki()}}
    >Resetoi kaikki</Button></Box>
    </>
    )
}

export default Otsikko